import React from 'react';
import Post from './Post/post';
import classes from './myposts.module.css';
import ReduxProfileForm from "../ProfileInfo/ProfileForm/ProfileForm";

const MyPosts = React.memo(props => {

    let NewPostData = props.ProfilePage.PostData.map(
        text => <Post message={text.message} key={text.id}
                           likescount={text.likescount}/>)

    const onSubmit = (formData) => {
        props.AddPostFunc(formData.profileMessage)
    }
    return (
        <div className={classes.header}>
            <div >
                Мои посты
            </div>
            <div>
                <ReduxProfileForm {...props} onSubmit={onSubmit}/>
            </div>
            <div className={classes.post}>
                <span>{NewPostData}</span>
            </div>
        </div>
    )
})

export default MyPosts;