import React from 'react';
import style from './post.module.css';

const Post = (props) => {
    return (
        <div className = {style.main}>
            <div className = {style.messages}>
                {props.message}
            </div>
            <div className = {style.posts}>
                <img src = 'https://liketime.ru/images/help/1.png' alt=''/> {props.likescount}
            </div>
        </div>
    )
};

export default Post;